import React from 'react';
import axios from 'axios';
import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom'
import styled from "styled-components";
import ResetPass from './Resetpass';

const Confirmation = () => {


  const [Verified, setVerified] = useState(false);
  const { ActivateAccount } = useParams();


  useEffect(() => {
    axios.get(`/api/user/checkPasswordResetTokenExist/${ActivateAccount}`)
      .then(res => {
        if (res.data.success === 'user exists') {
          console.log(res.data.success)
          setVerified(true)
          //window.location = 'http://writeups.securinetsesprit.com/Auth#/sign-in';
        }
        else
          alert(res.data.error)

      })
  })


  return (
    <div style={{ marginTop: '10vh' }}>
      <H>
        {Verified ? "Verified!" : "Verifying ... Please wait. "}
      </H>
      {Verified ? <ResetPass /> : null}
    </div >
  );
}

const H = styled.h1`
  margin: 100px ;
  text-align: center;
`;

export default Confirmation;
