import React from 'react';
import './resetpass.scss';
import { useState } from 'react';
import { useParams } from 'react-router-dom';
import axios from 'axios';


function ResetPass() {

    const [Pass, setPass] = useState("test");
    const { ActivateAccount } = useParams();
    // capture pass
    const check = (event) => {
        setPass(event.target.value)
    }

    //send request
    const change = () => {
        console.log(Pass)
        axios.patch(`/api/user/UpdatePasswordResetuser`, {
            resetToken: ActivateAccount,
            password: Pass
        })

            .then(res => {
                if (res.data.success === true) {
                    alert('password changed!');
                    window.location = 'http://writeups.securinetsesprit.com/Auth#/sign-in';
                }
            })
    }


    return (

        <div className="confirm__container reset-pass">
            <h1>Enter new password</h1>
            <span className="reset-password-form__label__label">New password</span>
            <input onChange={check} type="password" className="reset-password-form__label__input" name='pass1' />

            <br></br>
            <button onClick={change} className="reset-password-form__submit-button">
                change password
            </button>
        </div >

    );
}

export default ResetPass;