import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from 'axios';
import ReactLoading from "react-loading";
var CryptoJS = require("crypto-js");



class SignInForm extends Component {


    constructor() {

        super();


        this.state = {
            username: "",
            password: "",
            Loading: false,
            verification: false,
            verification1: false

        };
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleChange(event) {
        let target = event.target;
        let value = target.type === "checkbox" ? target.checked : target.value;
        let name = target.name;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {

        event.preventDefault();

        this.setState({ Loading: true });
        this.setState({ verification: false });
        this.setState({ verification1: false });



        try {
            axios.post(`/api/user/login`, { 'email': this.state.username, 'password': this.state.password })

                .then(res => {
                    if (res.data.token) {
                        let token = res.data.token;
                        let data_username = res.data.username;
                        let data_id = res.data.id;
                        let data_team = res.data.team;


                        // Encrypt username
                        var crypted_username = CryptoJS.AES.encrypt(JSON.stringify(data_username), 'my-secret-key@123').toString();

                        // Encrypt id
                        var crypted_id = CryptoJS.AES.encrypt(JSON.stringify(data_id), 'my-secret-key@123').toString();


                        this.setState({ Loading: false });
                        localStorage.setItem('tYEuDFZNSysbgeh82mkxeXTZAJNb0Hpb8KssSNTH', crypted_id);
                        localStorage.setItem('HYZn4A5fpSY68whsRGvZTxNGsbJO7lMUu1Vv1a6yfkadE2T', crypted_username);
                        localStorage.setItem('tYEuDFZNSysbgeh82mkxeXTZAJNb0Hpb8KssSNTHToken', token);
                        localStorage.setItem('tYEuDFZNSysbgeh82mkxeXTZAJNb0Hpb8KssSNTHTeam', data_team);


                        ///////create distractions
                        localStorage.setItem('tYEuDFZNSysbgeh82mkxeXTZAJNb0Hpb8pjogsdjpgdspjosgKssSNTH', 'HYZn4A5fpSY68whsRGvZTxNGsbJO7lMUu1Vv1a6fqsqsfyfkadE2T');
                        localStorage.setItem('HYZn4A5fpSY68whsRGvZTxNGsbJO7lMUuhjqiopiogdsjgppqog1Vv1a6yfkadE2T', 'tYEuDFZNSysbgeh82mkxeXTZAJNb0Hpb8pjogsdjpgdspjosgKssSNTH');
                        localStorage.setItem('tYEuDFZNSysbgeh82mkxeXTZAJNlnkfqsb0Hpb8pjogsdjpgdspjosgKssSNTH', 'HYZn4A5fpSY68whsRGvZTxNGsbJO7lMUuklqfqf1Vv1a6yfkadE2T');
                        localStorage.setItem('HYZn4A5fpSY68whlnkfqsRGvZTxNGsbJO7lMUuhjqiopiogdsjgppqog1Vv1a6yfkadE2T', 'tYEuDFZNSysbgeh82mkxeXTZAJNb0Hpb8pfqfqsjogsdjpgdspjosgKssSNTH');
                        localStorage.setItem('tYEuDFZNSysbgfqsqsfqseh82mkxeXTZAJNb0Hpb8pjogsdjpgdspjosgKssSNTH', 'HYZn4A5fpSY68whsRGvZTxNGsbJO7lMUqfsqsfu1Vv1a6yfkadE2T');
                        localStorage.setItem('HYZn4A5fpSYqsfqfsqsf68whsRGvZTxNGsbJO7lMUuhjqiopiogdsjgppqog1Vv1a6yfkadE2T', 'tYEuDFZqsfqsfsfqNSysbgeh82mkxeXTZAJNb0Hpb8pjogsdjpgdspjosgKssSNTH');


                        window.location = '/';

                    }
                    else if (res.data.success === true && res.data.token === true) {

                        this.setState({ verification: true });
                        this.setState({ Loading: false });

                    }

                }).finally(res => {

                    let token = localStorage.getItem('tYEuDFZNSysbgeh82mkxeXTZAJNb0Hpb8KssSNTH');
                    if (!token) {

                        this.setState({ verification1: true });
                        this.setState({ Loading: false });

                    }
                }


                )

        } catch (error) {
            throw error;


        }


    }
    render() {
        return (
            <div className="formCenter">
                <form className="formFields" onSubmit={this.handleSubmit}>
                    <div className="formField">
                        <label className="formFieldLabel" htmlFor="email">
                            E-Mail Address
                        </label>
                        <input
                            type="email"
                            id="username"
                            className="formFieldInput"
                            placeholder="Enter your email"
                            name="username"
                            value={this.state.username}
                            onChange={this.handleChange}
                        />
                    </div>
                    <div className="formField">
                        <label className="formFieldLabel" htmlFor="password">
                            Password
                        </label>
                        <input
                            type="password"
                            id="password"
                            className="formFieldInput"
                            placeholder="Enter your password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                        />
                    </div>
                    <div className="formField">
                        <button className="formFieldButton">Sign In</button>{" "}
                        <Link to="/sign-up" className="formFieldLink">
                            Create an account
                        </Link>
                        <br></br>
                        <a href="/reset-password" className="formFieldLink">
                            Forget password?
                        </a>
                        {this.state.verification1 ? <p style={{ color: "#FF6666", marginTop: "10px" }}>error! </p> : null}

                        {this.state.verification ? <p style={{ color: "#FF6666", marginTop: "10px" }}>Account not yet confirmed! </p> : null}

                        <div style={{ marginLeft: '35%' }}>
                            {this.state.Loading ? <ReactLoading type="bars" color="green" width={'35%'} /> : null}
                        </div>
                    </div>
                </form>
            </div >
        );
    }
}

export default SignInForm;
