import React, { Component } from "react";
import { Link } from "react-router-dom";
import axios from 'axios';
import ReactLoading from "react-loading";



class SignUpForm extends Component {
    constructor() {
        super();

        this.state = {
            email: "",
            password: "",
            username: "",
            Loading: false,
            team: "",
            teams: [],
            LoadingTeams: true,
            FirstTeam: "",

        };



        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);



        axios.get(`https://cyberleague.securinetsesprit.com/api/v1/scoreboard/top/99`)
            .then(res => {
                if (res.data.success) {
                    let array = [];
                    for (let i = 1; i <= Object.keys(res.data.data).length; i++) {
                        array.push(res.data.data[i].name)
                    }
                    this.setState({ teams: array });
                    this.setState({ LoadingTeams: false });
                    this.setState({ FirstTeam: res.data.data[1].name });




                }
            })
    }


    handleChange(event) {
        let target = event.target;
        let value = target.type === "checkbox" ? target.checked : target.value;
        let name = target.name;

        this.setState({
            [name]: value
        });
    }

    async handleSubmit(e) {
        this.setState({ Loading: true });

        e.preventDefault();


        try {
            this.state.phoneNumber = parseInt(this.state.phoneNumber);
            axios.post(`/api/user/register`, { 'email': this.state.email, 'password': this.state.password, 'username': this.state.username, 'team': this.state.team ? this.state.team : this.state.FirstTeam })
                .then(res => {
                    if (res.data.success === true) {
                        this.setState({ Loading: false });
                        window.location = '/Auth#/sign-in';

                    }
                    else
                        alert(res.data.error)
                }).catch(err => {
                    alert(err)
                })
        } catch (error) { throw error; }
    }

    render() {
        return (
            <div className="formCenter">
                <form onSubmit={this.handleSubmit} className="formFields">


                    <div className="formField">
                        <label className="formFieldLabel" htmlFor="name">
                            Username
                        </label>
                        <input
                            type="text"
                            id="username"
                            className="formFieldInput"
                            placeholder="Enter your username"
                            name="username"
                            value={this.state.username}
                            onChange={this.handleChange}
                            required

                        />
                    </div>


                    <div className="formField">
                        <label className="formFieldLabel" htmlFor="email">
                            E-Mail Address
                        </label>
                        <input
                            type="email"
                            id="email"
                            className="formFieldInput"
                            placeholder="Enter your email"
                            name="email"
                            value={this.state.email}
                            onChange={this.handleChange}
                            required

                        />
                    </div>


                    <div className="formField">
                        <label className="formFieldLabel" for="team">Choose your team:</label>

                        {this.state.LoadingTeams ? <> <ReactLoading type="bars" color="green" /> <h6>fetching teams...</h6></> : <select name='team' onChange={this.handleChange} style={{ backgroundColor: 'whitesmoke', borderRadius: "5px" }} >
                            {this.state.teams.map(item => {
                                return (<option value={item}>{item}</option>);
                            })}
                        </select>}



                    </div>




                    <div className="formField">
                        <label className="formFieldLabel" htmlFor="password">
                            Password
                        </label>
                        <input
                            type="password"
                            id="password"
                            className="formFieldInput"
                            placeholder="Enter your password"
                            name="password"
                            value={this.state.password}
                            onChange={this.handleChange}
                            required
                            minLength={4}

                        />
                    </div>

                    <div className="formField">
                        <button className="formFieldButton">Sign Up</button>{" "}
                        <Link to="/sign-in" className="formFieldLink">
                            I'm already member
                        </Link>
                        <div style={{ marginLeft: '35%' }}>
                            {this.state.Loading ? <ReactLoading type="bars" color="green" width={'35%'} /> : null}
                        </div>
                    </div>
                </form>

            </div>
        );
    }
}
export default SignUpForm;
