import React from 'react';
import '../resetpass.scss';
import { useState } from 'react';
import axios from 'axios';
import ReactLoading from "react-loading";


function ForgetPass() {

    const [Email, setEmail] = useState("");
    const [Loading, setLoading] = useState(false);

    // capture email
    const check = (event) => {
        setEmail(event.target.value)
    }

    //send request
    const change = () => {

        setLoading(true)


        axios.post(`/api/user/ResetPassword/${Email}`)

            .then(res => {
                if (res.data.success) {
                    alert(res.data.success);
                    setLoading(false)
                    window.location = '/';
                }
            })
    }

    return (

        <div className="confirm__container reset-pass">
            <h1>Enter your email</h1>
            <span className="reset-password-form__label__label">email</span>
            <input onChange={check} type="text" className="reset-password-form__label__input" name='pass1' />

            <br></br>
            <button onClick={change} className="reset-password-form__submit-button">
                send email
            </button>
            <div style={{ marginLeft: '35%' }}>
                {Loading ? <div><ReactLoading type="bars" color="green" /> <h6>Sending... check spam </h6></div> : null}
            </div>
        </div >

    );
}

export default ForgetPass;